import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import Vue from 'vue'
import {
	$http
} from '@escook/request-miniprogram' // 引入第三方包

Vue.config.productionTip = false


App.mpType = 'app'
const app = new Vue({
	...App
})
app.$mount()


import {
	createSSRApp
} from 'vue'
export function createApp() {
	const app = createSSRApp(App)
	return {
		app
	}
}

uni.$http = $http //挂载到顶级对象上

$http.baseUrl = 'https://www.uinav.com' // 配置请求根路径
// 请求开始之前做一些事情
$http.beforeRequest = function(options) {
	uni.showLoading({
		title: '数据加载中',
	})
}

// 请求完成之后做一些事情
$http.afterRequest = function() {
	wx.hideLoading()
}